.jonnitto-prettyembedvimeo {
  position: relative;
  display: block;
  width: 100%;
  max-width: 100%;
}

.jonnitto-prettyembedvimeo img {
  width: 100%;
  height: auto;
}

.jonnitto-prettyembedvimeo__button::after {
  display: block;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.8;
  transform: scale(0.8);
  background: inline("PlayButton.png") center no-repeat;
  background-size: size("PlayButton.png", 2);
  transition: transform 0.25s ease-in-out, opacity 0.25s linear;
  pointer-events: none;
}

.jonnitto-prettyembedvimeo__button:hover::after {
  opacity: 1;
  transform: scale(1);
}

.jonnitto-prettyembedvimeo--play::after {
  display: none;
}

.jonnitto-prettyembedvimeo--inline iframe {
  border: 0 solid transparent;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.jonnitto-prettyembedvimeo__lightbox {
  display: block;
  position: fixed 0;
  content: "";
  background: rgba(11, 11, 11, 0.8);
  z-index: -1;
  opacity: 0;
  font-size: 0;
  transition: opacity 0.2s, z-index 0s 0.2s;
  text-align: center;
  overflow: hidden;
  overflow-y: auto;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
}

.jonnitto-prettyembedvimeo__lightbox > * {
  display: inline-block;
  white-space: normal;
  vertical-align: middle;
  text-align: left;
}

.jonnitto-prettyembedvimeo__lightbox::before {
  display: inline-block;
  overflow: hidden;
  width: 0;
  height: 100%;
  vertical-align: middle;
  content: "";
}

.jonnitto-prettyembedvimeo__lightbox-visible .jonnitto-prettyembedvimeo__lightbox {
  z-index: 1149;
  opacity: 1;
  transition: opacity 0.2s;
}

.jonnitto-prettyembedvimeo__lightbox-inner {
  padding-top: 0;
  padding-bottom: 0;
  position: relative;
  max-width: 900px;
  max-height: 90%;
  z-index: -1;
  opacity: 0;
  transform: scale(0);
  transition: opacity 0.2s, transform 0.2s, z-index 0s 0.2s;
  width: 100%;
}

.jonnitto-prettyembedvimeo__lightbox-visible .jonnitto-prettyembedvimeo__lightbox-inner {
  z-index: 1150;
  opacity: 1;
  transform: scale(1);
  transition: opacity 0.2s, transform 0.2s;
}

.jonnitto-prettyembedvimeo__lightbox-content {
  line-height: 0;
  width: 100%;
  max-width: 900px;
}

.jonnitto-prettyembedvimeo__lightbox-close {
  top: -40px;
  user-select: none;
  overflow: visible;
  cursor: pointer;
  background: transparent;
  border: 0;
  -webkit-appearance: none;
  display: block;
  outline: none;
  z-index: 1146;
  box-shadow: none;
  width: 100%;
  height: 44px;
  line-height: 44px;
  position: absolute;
  text-decoration: none;
  opacity: 0.65;
  padding: 0 0 18px 10px;
  color: #fff;
  right: -6px;
  text-align: right;
  font-style: normal;
  font-size: 28px;
  font-family: Arial, Baskerville, monospace;
}

.jonnitto-prettyembedvimeo__lightbox-close:hover, .jonnitto-prettyembedvimeo__lightbox-close:focus, .jonnitto-prettyembedvimeo__lightbox-close:active {
  opacity: 1;
  outline: none;
}

.jonnitto-prettyembedvimeo__lightbox-close::-moz-focus-inner {
  padding: 0;
  border: 0;
}

.jonnitto-prettyembedvimeo__lightbox-holder {
  display: block;
  width: 100%;
  height: 0;
  overflow: hidden;
  padding-top: 56.25%;
}

.jonnitto-prettyembedvimeo__lightbox-holder iframe {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
  background: #000;
}
